<template>
    <div class="row align-items-center justify-content-center master-row">
        <div class="col-12 col-sm-6 col-md-6 col-lg-6 entry-element" @click="redirect">
            <h1 class="animated">A | N</h1>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Home',
  methods: {
    redirect () {
      this.$router.push('About')
    }
  }
}
</script>

<style scoped>
    .master-row {
        height: 80vh;
        text-align: center;
    }

    .entry-element {
        transition: 0.3s ease;
    }

    .entry-element:hover {
        transform: scale(1.2);
    }

    h1 {
        font-size: 512px;
        -webkit-text-stroke: 2px red;
        animation-name: showcase;
        animation-duration: 5000ms;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
        cursor: pointer;
    }

    @keyframes showcase {
        from {
                color: #0D131A;
            }
        50% {
                color: #0D131A;
                -webkit-text-stroke: 2px blue;
            }
        to {
                color: #0D131A;
            }
    }
</style>



