<template>
  <div class="topbar row justify-content-center">
    <Burger></Burger>
  </div>
</template>

<script>
import Burger from './Burger'

export default {
  components: {
    'Burger': Burger
  },
  data () {
    return {
      profiles: [
        {
          id: 0,
          link: 'https://github.com/azadnalbandian',
          icon: 'header-icon fab fa-github fa-2x'
        },
        {
          id: 1,
          link: 'https://www.linkedin.com/in/azad-nalbandian-057a4b149/',
          icon: 'header-icon fab fa-linkedin fa-2x'
        },
        {
          id: 2,
          link: 'mailto:azadnalbandian@gmail.com',
          icon: 'header-icon fas fa-at fa-2x'
        }
      ]
    }
  },
  computed: {
    currentRouteName () {
      return this.$route.name
    }
  }
}
</script>

<style scoped>
  .topbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 70px;
    z-index: 999;
  }

</style>



