<template>
    <div class="container">
        <div class="row">
            <div class="col-lgl-10 col-md-10 offset-md-1">
                <ul class="timeline">
                    <li class="mt-5 mb-5">
                        <a target="_blank" href="https://www.jatech.co.uk/">Jatech Ltd., 08/20 - Present</a>
                        <h4 class="mt-2">Software Engineer</h4>
                        <p>Currently, I continue to provide web development and system architecture services for our clients, as well as offer Azure support.</p>
                    </li>
                    <li class="mt-5 mb-5">
                        <a target="_blank" href="https://www.jatech.co.uk/">Jatech Ltd., 04/20 - 08/20</a>
                        <h4 class="mt-2">Junior Software Engineer</h4>
                        <p>After moving to Jatech, I began working as a full software engineer, developing new products and maintaining existing products for clients. I led and completed a project to port a deprecated AngularJS system to our custom framework as my first major project, as well as implementing several entirely new features.</p>
                    </li>
                    <li class="mt-5 mb-5">
                        <a target="_blank" href="https://www.pagesuite.com/">PageSuite Ltd., 06/19 - 04/20</a>
                        <h4 class="mt-2">Support Developer</h4>
                        <p>As Support Developer, my job was to provide support for our main development team as bugmaster and third-line support lead. I dealt with system-critical backend issues, wrote C# APIs and programs for .NET Framework to automate regular tasks and fulfil client requests using AWS, performed SQL atabase reporting and administration, and maintained existing WinForms projects for internal usage.</p>
                    </li>
                    <li class="mt-5 mb-5">
                        <a target="_blank" href="https://www.pagesuite.com/">PageSuite Ltd., 04/18 - 06/19</a>
                        <h4 class="mt-2">Second-line Support Technician</h4>
                        <p>In this role, I was tasked with handling client support and escalation requests, including web debugging (primarily using Charles), IIS server administration, QA, and scripting.</p>
                    </li>
                    <li class="mt-5 mb-5">
                        <a target="_blank" href="https://www.pagesuite.com/">PageSuite Ltd., 06/17 - 04/18</a>
                        <h4 class="mt-2">First-line Support Technician</h4>
                        <p>My first position in the IT industry. In this role, my main duties were to provide end-user technical support for a bespoke PDF rendering project, on iOS, Android and PC devices.</p>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  'name': 'Timeline'
}
</script>

<style scoped>

ul.timeline {
    list-style-type: none;
    position: relative;
}

ul.timeline:before {
    content: ' ';
    background: #d4d9df;
    display: inline-block;
    position: absolute;
    left: 29px;
    width: 2px;
    height: 100%;
    z-index: 400;
}

ul.timeline > li {
    margin: 20px 0;
    padding-left: 40px;
}

ul.timeline > li:before {
    content: ' ';
    background: lightblue;
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    border: 3px solid lightblue;
    left: 15px;
    width: 30px;
    height: 30px;
    z-index: 400;
}

a:hover {
    padding-left: 1rem;
}

</style>



