<template>
  <div class="container-fluid container-head" id="app">
    <NavigationBar v-if="this.$route.name != 'Home'"></NavigationBar>
    <Sidebar></Sidebar>
    <router-view/>
  </div>
</template>

<script>
import NavigationBar from './components/Navigation/NavigationBar'
import Sidebar from './components/Navigation/Sidebar'

export default {
  name: 'App',
  components: {
    'NavigationBar': NavigationBar,
    'Sidebar': Sidebar
  }
}
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}

i {
  margin-right: 20px;
}

  .box-content {
    padding: 2em;
    border-radius: 5px;
  }
</style>



