          image: 'https://icons-for-free.com/iconfiles/png/512/mario+mario+bros+mario+world+mushroom+toad+videogame+icon-1320196400529338074.png'
        },
        {
          id: 3,
          name: 'Project D',
          link: 'www.google.com',
          text: 'This is some accompaniment text',
          image: 'https://icons-for-free.com/iconfiles/png/512/mario+mario+bros+mario+world+mushroom+toad+videogame+icon-1320196400529338074.png'
        }
      ]
    }
  }
}
</script>

<style scoped>
  .card {
    background-color: rgba(0,0,0,0);
    border: 0;
  }
</style>



